.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 50;
  display: none;
}

.popup__inderlay {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  background: #FFFFFF;
  opacity: 0.8;
}

.popup__container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  width: 358px;
  padding: 44px 52px;
  box-shadow: 0px -13px 20px rgba(10, 12, 49, 0.03), 10px 20px 50px rgba(0, 34, 155, 0.06);
  z-index: 10;
  text-align: center;
  background-color: #fff;
}

.popup__img {
  svg {
    width: 87px;
    height: 79px;
  }
}

.popup__title {
  margin-top: 28px;
  font-size: 39px;
  line-height: 52px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.popup__text {
  margin-top: 41px;
}

.popup__button {
  height: 55px;
  font-size: 22px;
  margin-top: 42px;
  span {
    font-weight: 700;
    font-family: 'Quicksand', sans-serif;
  }
}


@media screen and (max-width: 1230px) {
  .popup__container {
    width: 29.10vw;
    padding: 3.57vw 4.22vw;
    box-shadow: 0 -1.05vw 1.62vw rgba(10, 12, 49, 0.03), 10px 20px 50px rgba(0, 34, 155, 0.06);
  }
  .popup__img {
    svg {
      width: 7.07vw;
      height: 6.42vw;
    }
  }
  .popup__title {
    margin-top: 2.27vw;
    font-size: 3.17vw;
    line-height: 4.22vw;
  }
  .popup__text {
    margin-top: 3.33vw;
  }
  .popup__button {
    height: 4.47vw;
    font-size: 1.78vw;
    margin-top: 3.41vw;
  }
}


@media screen and (max-width: 600px) {
  .popup__container {
    width: 88.61vw;
    padding: 12.22vw 8.88vw;
    box-shadow: 0 -5.55vw 13.88vw rgba(10, 12, 49, 0.03), 10px 20px 50px rgba(0, 34, 155, 0.06);
  }
  .popup__img {
    svg {
      width: 23.88vw;
      height: 21.94vw;
    }
  }
  .popup__title {
    margin-top: 7.77vw;
    font-size: 10.83vw;
    line-height: 14.44vw;
  }
  .popup__text {
    margin-top: 11.11vw;
  }
  .popup__button {
    height: 15.27vw;
    font-size: 6.11vw;
    margin-top: 11.66vw;
  }
}