.faq__container {
  padding-top: 131px;
  padding-bottom: 124px;
  justify-content: space-between;
}

.faq__left {
  width: 310px;
  padding-top: 46px;
}

.faq__title {
  font-size: 55px;
  line-height: 67px;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.faq__text {
  margin-top: 21px;
}

.faq__button {
  width: 100%;
  height: 55px;
  font-size: 22px;
  margin-top: 23px;
}

.faq__acc {
  width: 770px;
  border: 1px solid #CACEE0;
}

.faq__item {
  border-bottom: 1px solid #CACEE0;
  position: relative;
  transition: .3s;

  &.activeAcc {
    background-color: #F4F6FF;

    .faq__contpm {
      &::after {
        opacity: 0;
      }
    }
  }

  &:last-child {
    border: none;
  }

}

.faq__controller {
  padding: 21px 25px;
  padding-right: 115px;
  line-height: 28px;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  cursor: pointer;
}

.faq__ansver {
  padding: 21px 25px;
  font-size: 16px;
  line-height: 24px;
  padding-right: 115px;
  padding-top: 0;
  display: none;
}

.faq__cont {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 73px;
  border-left: 1px solid #CACEE0;
}

.faq__contpm {
  height: 72px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    height: 2px;
    width: 24px;
    border-radius: 26px;
    background-color: #082854;
  }

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 2px;
    height: 24px;
    border-radius: 26px;
    background-color: #082854;
    transition: .3s;
  }
}


@media screen and (max-width: 1230px) {
  .faq__container {
    padding-top: 10.65vw;
    padding-bottom: 10.08vw;
  }
  .faq__left {
    width: 25.2vw;
    padding-top: 3.74vw;
  }
  .faq__title {
    font-size: 4.47vw;
    line-height: 5.45vw;
  }
  .faq__text {
    margin-top: 1.71vw;
  }
  .faq__button {
    height: 4.47vw;
    font-size: 1.79vw;
    margin-top: 1.87vw;
  }
  .faq__acc {
    width: 62.6vw;
  }
  .faq__controller {
    padding: 1.71vw 2.03vw;
    padding-right: 9.35vw;
    line-height: 2.28vw;
  }
  .faq__ansver {
    padding: 1.71vw 2.03vw;
    font-size: 1.3vw;
    line-height: 1.95vw;
    padding-right: 9.35vw;
  }
  .faq__cont {
    width: 5.93vw;
  }
  .faq__contpm {
    height: 5.85vw;
    &::before {
      height: 0.16vw;
      width: 1.95vw;
      border-radius: 2.11vw;
    }

    &::after {
      width: 0.16vw;
      height: 1.95vw;
      border-radius: 2.11vw;
    }
  }
}



@media screen and (max-width: 600px) {
  .faq__container {
    padding-top: 23.89vw;
    padding-bottom: 18.89vw;
  }
  .faq__left {
    width: 100%;
    padding-top: 0;
    text-align: center;
  }
  .faq__title {
    font-size: 10.83vw;
    line-height: 14.44vw;
  }
  .faq__text {
    margin-top: 4.72vw;
  }
  .faq__button {
    height: 15.28vw;
    font-size: 6.11vw;
    margin-top: 8.89vw;
  }
  .faq__acc {
    width: 100%;
    margin-top: 18.61vw;
  }
  .faq__controller {
    padding: 2.22vw 3.89vw;
    padding-right: 22.83vw;
    font-size: 5vw;
    line-height: 7.22vw;
    min-height: 19.44vw;
    display: flex;
    align-items: center;
    align-content: center;
  }
  .faq__ansver {
    padding: 5vw 3.89vw;
    padding-top: 0;
    font-size: 4.44vw;
    line-height: 6.67vw;
    padding-right: 20.83vw;
  }
  .faq__cont {
    width: 19.44vw;
  }
  .faq__contpm {
    height: 19.44vw;
    &::before {
      height: 0.56vw;
      width: 6.67vw;
      border-radius: 7.22vw;
    }

    &::after {
      width: 0.56vw;
      height: 6.67vw;
      border-radius: 7.22vw;
    }
  }
}