.section__invite {
  position: relative;
  z-index: 5;
}

.invite__container{
  margin: 0 -88px;
  padding: 52px;
  padding-right: 68px;
  border-radius: 35px;
  min-height: 395px;
  background: #F3F6FF;
  position: relative;
  justify-content: flex-end;
}

.invite__content {
  width: 522px;
}

.invite__title {
  font-size: 45px;
  line-height: 64px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.invite__button {
  margin-top: 38px;
  width: 310px;
  height: 55px;
  font-size: 22px;
}

.invite__list {
  top: -26px;
  left: 40px;
  position: absolute;
}

.invite__item {
  width: 498px;
  height: 102px;
  box-shadow: 0 20px 40px rgba(0, 48, 192, 0.1);
  border-radius: 7px;
  padding: 15px 30px;
  margin-bottom: 31px;
  align-items: center;
  align-content: center;
  flex-wrap: nowrap;
  background-color: rgba(255, 255, 255, .75);
}

.invite__nym {
  width: 70px;
  height: 70px;
  line-height: 70px;
  font-size: 40px;
  box-shadow: 0 20px 40px rgba(61, 60, 106, 0.1);
  border-radius: 7px;
  opacity: .75;
  background: #437FEC;
  text-align: center;
  color: #fff;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.invite__strokes {
  padding: 22px;

  .in__stroke {
    width: 88px;
    height: 20px;
    box-shadow: 0 20px 40px rgba(61, 60, 106, 0.1);
    border-radius: 7px;
    margin-bottom: 8px;
    background-color: #E3E7FF;
    opacity: .75;

    &:last-child {
      width: 263px;
      margin-bottom: 0;
    }
  }
}

.init_2 {
  margin-left: 93px;
  .invite__nym {
    background-color: #48B2ED;
  }
  .invite__strokes {
    .in__stroke {
      &:last-child {
        width: 328px;
      }
    }
  }
}

.init_3 {
  width: 478px;
  margin-left: 19px;
  .invite__nym {
    background-color: #4BD3ED;
  }
  .invite__strokes {
    .in__stroke {
      width: 128px;
      &:last-child {
        width: 241px;
      }
    }
  }
}



