    padding-bottom: 13.06vw;
    padding-top: 70vw;
    border-radius: 0;
    min-height: auto;
    display: block;
    text-align: center;
  }
  .invite__content {
    width: 100%;
  }
  .invite__title {
    font-size: 9.72vw;
