    border-radius: 1.94vw;
  }
  .invite__strokes {
    padding: 0 3.33vw;
    .in__stroke {
      width: 13.33vw;
      height: 3.06vw;
      box-shadow: 0 5.56vw 11.11vw rgba(0, 48, 192, 0.1);
      border-radius: 1.94vw;
      margin-bottom: 1.39vw;
      &:last-child {
        width: 40vw;
      }
    }
  }
  .init_2 {
    margin-left: 14.17vw;
    .invite__strokes {
      .in__stroke {
        &:last-child {
          width: 50vw;
        }
      }
    }
  }
  .init_3 {
    width: 73.06vw;
    margin-left: 3.06vw;
    .invite__strokes {
      .in__stroke {
        width: 19.44vw;
        &:last-child {
          width: 36.94vw;
        }
      }
    }
  }
}