.section__file {
  border-bottom: 1px solid #D2D8F0;
}

.file__contaner {
  padding-top: 112px;
  padding-bottom: 155px;
  position: relative;
}

.file__content {
  width: 454px;
  position: relative;
  z-index: 10;
}

.file__title {
  font-size: 55px;
  line-height: 67px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.file__list {
  margin-top: 20px;

  li {
    width: 190px;
    font-size: 20px;
    line-height: 38px;
    position: relative;
    padding: 0 18px;
    padding-right: 6px;

    &::before {
      position: absolute;
      content: '';
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: $red;
    }
  }
}

.file__dec {
  width: 700px;
  height: 422px;
  top: 55px;
  right: -110px;
  position: absolute;
  z-index: 2;

  img {
    width: 100%;
    height: 100%;
  }
}

.file__bg {
  width: 1245px;
  height: 442px;
  bottom: 0;
  right: -324px;
  position: absolute;

  img {
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 1230px) {
  .file__contaner {
    padding-top: 9.11vw;
    padding-bottom: 12.6vw;
  }
  .file__content {
    width: 36.91vw;
  }
  .file__title {
    font-size: 4.47vw;
    line-height: 5.45vw;
  }
  .file__list {
    margin-top: 1.63vw;
    li {
      width: 15.45vw;
      font-size: 1.63vw;
      line-height: 3.09vw;
      padding: 0 1.46vw;
      padding-right: 0.49vw;
      &::before {
        width: 0.49vw;
        height: 0.49vw;
      }
    }
  }
  .file__dec {
    width: 56.91vw;
    height: 34.31vw;
    top: 4.47vw;
    right: -8.94vw;
  }
  .file__bg {
    width: 101.22vw;
    height: 35.93vw;
    right: -26.34vw;
  }
}


@media screen and (max-width: 600px) {
  .file__contaner {
    padding-top: 81.67vw;
    padding-bottom: 11.39vw;
  }
  .file__content {
    width: 100%;
  }
  .file__title {
    font-size: 10.83vw;
    line-height: 14.44vw;
  }
  .file__list {
    margin-top: 3.61vw;
    li {
      width: 100%;
      font-size: 5.56vw;
      line-height: 10.56vw;
      padding: 0 5vw;
      padding-right: 0;
      &::before {
        width: 1.67vw;
        height: 1.67vw;
      }
    }
  }
  .file__dec {
    width: 98.06vw;
    height:59.17vw;
    top: 4.17vw;
    left: 0.83vw;
    right: auto;
  }
  .file__bg {
    display: none;
  }
}