.footer {
  background-color: #F3F6FF;
}

.footer__content {
  padding-top: 57px;
  padding-bottom: 50px;
  justify-content: space-between;
}

.footer__left {
  width: 340px;
}

.footer__logo {
  display: flex;
  align-items: center;
  align-content: center;

  img {
    width: 49px;
    margin-right: 13px;
  }

  span {
    font-size: 31px;
    line-height: 38px;
    color: #434242;
    font-weight: 700;
    font-family: 'Quicksand', sans-serif;

    span {
      color: $red;
    }
  }
}

.footer__soc {
  margin-top: 28px;
  width: 202px;

  li {
    margin-right: 22px;
    margin-bottom: 22px;
  }

  a {
    display: flex;

    &:hover {
      svg {
        path {
          fill: $red;
        }
      }
    }

    svg {
      width: 45px;
      height: 45px;
      path {
        transition: .3s;
      }
    }
  }
}

.footer__nav {
  padding-top: 20px;

  li {
    margin-bottom: 24px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  a {
    font-weight: 500;
    color: #082854;
    font-family: 'Quicksand', sans-serif;
    transition: .3s;

    &:hover {
      color: $red;
    }
  }
}

.signup__form {
  padding-top: 15px;
  width: 497px;
}

.signup__title {
  font-size: 24px;
  line-height: 42px;
  color: #082854;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.signup__group {
  margin-top: 23px;
  position: relative;
}

.signup__input {
  height: 50px;
  border-radius: 42px;
  padding-left: 30px;
  padding-right: 180px;
  font-size: 16px;
  background: #FFFFFF;
  border: 1px solid #082854;
}

.signup__button {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 50px;
  border-radius: 42px;
  text-align: center;
  line-height: 50px;
  width: 172px;
  background-color: $red;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.footer__copyrhght {
  background-color: #082854;
}

.copyrhght {
  text-align: center;
  font-size: 18px;
  line-height: 42px;
  color: #fff;
  padding: 9px 0;
}


@media screen and (max-width: 1230px) {
  .footer__content {
    padding-top: 4.63vw;
    padding-bottom: 4.07vw;
  }
  .footer__left {
    width: 27.64vw;
  }
  .footer__logo {
    img {
      width: 3.98vw;
      margin-right: 1.06vw;
    }
    span {
      font-size: 2.52vw;
      line-height: 3.09vw;
    }
  }
  .footer__soc {
    margin-top: 2.28vw;
    width: 16.42vw;
    li {
      margin-right: 1.79vw;
      margin-bottom: 1.79vw;
    }
    a {
      svg {
        width: 3.66vw;
        height: 3.66vw;
      }
    }
  }
  .footer__nav {
    padding-top: 1.63vw;
    li {
      margin-bottom: 1.95vw;
    }
  }
  .signup__form {
    padding-top: 1.22vw;
    width: 40.41vw;
  }
  .signup__title {
    font-size: 1.95vw;
    line-height: 3.41vw;
  }
  .signup__group {
    margin-top: 1.87vw;
  }
  .signup__input {
    height: 4.07vw;
    border-radius: 3.41vw;
    padding-left: 2.44vw;
    padding-right: 14.63vw;
    font-size: 1.3vw;
  }
  .signup__button {
    height: 4.07vw;
    border-radius: 3.41vw;
    line-height: 4.07vw;
    width: 13.98vw;
    font-size: 1.46vw;
  }
  .copyrhght {
    font-size: 1.46vw;
    line-height: 3.41vw;
    padding: 0.73vw 0;
  }
}


@media screen and (max-width: 600px) {
  .footer__content {
    padding-top: 7.22vw;
    padding-bottom: 42.22vw;
    position: relative;
    width: 100%;
    justify-content: center;
  }
  .footer__part {
    width: 100%;
  }
  .footer__left {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .footer__logo {
    img {
      width: 8.33vw;
      margin-right: 1.67vw;
    }
    span {
      font-size: 5.83vw;
      line-height: 7.22vw;
    }
  }
  .footer__soc {
    position: absolute;
    bottom: 13.06vw;
    margin-top: 0;
    left: 0;
    right: 0;
    width: 100%;
    justify-content: space-between;
    li {
      margin: 0;
    }
    a {
      svg {
        width: 12.5vw;
        height: 12.5vw;
      }
    }
  }
  .footer__nav {
    padding-top: 9.72vw;
    width: 100%;
    li {
      margin-bottom: 6.67vw;
      font-weight: 500;
      text-align: center;
      width: 100%;
    }
  }
  .signup__form {
    padding-top: 13.89vw;
    width: 100%;
  }
  .signup__title {
    font-size: 6.67vw;
    line-height: 11.67vw;
  }
  .signup__group {
    margin-top: 6.39vw;
  }
  .signup__input {
    height: 13.89vw;
    border-radius: 11.67vw;
    padding-left: 8.06vw;
    padding-right: 37.5vw;
    font-size: 4.44vw;
    width: 100%;
  }
  .signup__button {
    height: 13.89vw;
    border-radius: 11.67vw;
    line-height: 13.89vw;
    width: 36.11vw;
    font-size: 5vw;
  }
  .copyrhght {
    font-size: 5vw;
    line-height: 11.67vw;
    padding: 1.11vw 0;
  }
}