.section__main {
  overflow: hidden;
}

.main__cintainer {
  padding-top: 50px;
  padding-bottom: 75px;
  padding-left: 30px;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  position: relative;
}

.main__left {
  width: 422px;
  padding-top: 10px;
  position: relative;
  z-index: 5;
}

.main__logo {
  img {
    width: 280px;
  }
}

.main__text {
  margin-top: 19px;
  font-size: 40px;
  line-height: 53px;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.main__bg {
  width: 901px;
  height: 906px;
  top: -185px;
  right: -253px;
  position: absolute;

  img {
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 1230px) {
  .main__cintainer {
    padding-top: 4.07vw;
    padding-bottom: 6.1vw;
    padding-left: 2.44vw;
  }
  .main__left {
    width: 34.31vw;
    padding-top: 0.81vw;
  }
  .main__logo {
    img {
      width: 22.76vw;
    }
  }
  .main__text {
    margin-top: 1.54vw;
    font-size: 3.25vw;
    line-height: 4.31vw;
  }
  .main__bg {
    width: 73.25vw;
    height: 73.66vw;
    top: -15.04vw;
    right: -20.57vw;
  }
}



@media screen and (max-width: 600px) {
  .main__cintainer {
    padding-top: 83.61vw;
    padding-bottom: 0;
    padding-left: 0;
  }
  .main__left {
    padding-bottom: 12.22vw;
    width: 100%;
    padding-top: 0;
  }
  .main__logo {
    img {
      width: 67.78vw;
    }
  }
  .main__text {
    margin-top: 3.06vw;
    font-size: 10.83vw;
    line-height: 14.44vw;
  }
  .main__bg {
    width: 100vw;
    height: 100.56vw;
    top: -20.56vw;
    right: -7.22vw;
  }
}