html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  position: relative;
}
body {
  overflow-x: hidden;
  position: relative;
}

* {
  box-sizing: border-box; }

a {
  text-decoration: none !important;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0; }

h1, h2, h3 {
  margin: 0;
  padding: 0;
  font-weight: normal; }

li {
  margin: 0;
  padding: 0;
  list-style: none; }

p {
  margin: 0;
}

input {
  outline: none;
  border: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

textarea {
  outline: none;
  border: none;
}

select {
  outline: none;
  border: none;
}

.wrapper {
  position: relative;
  overflow: hidden;
  z-index: 10;
}

.container {
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.flexCentr {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.cover {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contain {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hr {
  width: 100%;
}



//-------------------------//

$red: #E02222;

body {
  font-size: 20px;
  line-height: 30px;
  color: #315176;
  font-family: 'Lato', sans-serif;
}

.overBody {
  overflow: hidden;
}

.container {
  width: 1186px;
}

.red {
  color: $red;
}

////////
.button {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

input.error {
  border-color: $red;
}

.button__red {
  background-color: $red;
  color: #fff;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  border-radius: 7px;
  transition: .3s;

  &:hover {
    opacity: .8;
  }
}

.button_su{
  overflow: hidden;
  position: relative;
  border-radius: 7px;
}

.su_button_circle{
  background-color: $red;
  border-radius: 1000px;
  position: absolute;
  left:0;
  top:0;
  width: 0px;
  height: 0px;
  margin-left: 0px;
  margin-top: 0px;
  pointer-events: none;
  /*animation-timing-function: ease-in-out; */
}

.button_su_inner, .button_su_innerSmall{
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  background: $red;
  color: #F4F4F4;
  font-weight: normal;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: 400ms;
  z-index: 18;
}

.button_su_innerSmall{
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  background: $red;
  color: #F4F4F4;
  font-weight: normal;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: 400ms;
  z-index: 18;
}

.button_text_container{
  position:relative;
  z-index: 18;
}

.explode-circle {
  animation: explode 0.5s forwards;

}

.desplode-circle{
  animation: desplode 0.5s forwards;
}

.explode-circleSmall {
  animation: explodeSmall 0.5s forwards;

}

.desplode-circleSmall{
  animation: desplodeSmall 0.5s forwards;
}

@keyframes explode {
  0% {
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    background-color: rgba(42, 53, 80,0.8);
  }
  100% {
    width: 800px;
    height: 800px;
    margin-left: -400px;
    margin-top: -400px;
    background-color: rgba(8, 40, 84,0.8);
  }
}

@keyframes desplode {
  0% {
    width: 800px;
    height: 800px;
    margin-left: -400px;
    margin-top: -400px;
    background-color: rgba(8, 40, 84,0.8);
  }
  100% {
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    background-color: rgba(8, 40, 84,0.8);
  }
}

@keyframes explodeSmall {
  0% {
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    background-color: rgba(42, 53, 80,0.8);
  }
  100% {
    width: 400px;
    height: 400px;
    margin-left: -200px;
    margin-top: -200px;
    background-color: rgba(8, 40, 84,0.8);
  }
}

@keyframes desplodeSmall {
  0% {
    width: 400px;
    height: 400px;
    margin-left: -200px;
    margin-top: -200px;
    background-color: rgba(8, 40, 84,0.8);
  }
  100% {
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    background-color: rgba(8, 40, 84,0.8);
  }
}


