.section__integ {
  overflow: hidden;
  position: relative;
}

.integ__container {
  padding-top: 167px;
  padding-bottom: 160px;
}

.unteg__content {
  width: 454px;
  position: relative;
  z-index: 5;
}

.integ__title {
  font-size: 55px;
  line-height: 57px;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.integ__text {
  margin-top: 21px;
}

.integ__dec {
  width: 1245px;
  height: 524px;
  top: 85px;
  right: -242px;
  position: absolute;

  img {
    width: 100%;
    height: 100%;
  }
}

.integ__partnears {
  box-shadow: 10px 20px 30px rgba(0, 34, 155, 0.04);
}

.indecmob {
  display: none;
}


@media screen and (max-width: 1230px) {
  .integ__container {
    padding-top: 13.58vw;
    padding-bottom: 13.01vw;
    position: static;
  }
  .unteg__content {
    width: 36.91vw;
  }
  .integ__title {
    font-size: 4.47vw;
    line-height: 4.63vw;
  }
  .integ__text {
    margin-top: 1.71vw;
  }
  .integ__dec {
    width: 101.22vw;
    height: 42.6vw;
    top: 6.91vw;
    right: -19.67vw;
  }
  .integ__partnears {
    box-shadow: 0.81vw 1.62vw 2.44vw rgba(0, 34, 155, 0.04);
  }
}


@media screen and (max-width: 600px) {
  .integ__container {
    padding-top: 81.67vw;
    padding-bottom: 14.44vw;
  }
  .unteg__content {
    width: 100%;
  }
  .integ__title {
    font-size: 10.83vw;
    line-height: 14.44vw;
  }
  .integ__text {
    margin-top: 5.56vw;
    width: 82.5vw;
  }
  .indecmob {
    display: block;
  }
  .indec {
    display: none;
  }
  .integ__dec {
    width: 185.83vw;
    height: 109.72vw;
    top: -31.11vw;
    right: auto;
    left: -18.61vw;
  }
  .integ__partnears {
    box-shadow: 2.78vw 5.56vw 8.34vw rgba(0, 34, 155, 0.04);
  }
}