.section__sleep {
  overflow: hidden;
}

.sleep__container {
  padding-top: 182px;
  padding-bottom: 216px;
  position: relative;
}

.sleep__content {
  width: 354px;
}

.sleep__title {
  font-size: 45px;
  line-height: 60px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.sleep__text {
  margin-top: 30px;
  line-height: 37px;
}

.sleep__dec {
  width: 983px;
  height: 523px;
  top: 53px;
  right: -211px;
  position: absolute;

  img {
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 1230px) {
  .sleep__container {
    padding-top: 14.8vw;
    padding-bottom: 17.56vw;
  }
  .sleep__content {
    width: 28.78vw;
  }
  .sleep__title {
    font-size: 3.66vw;
    line-height: 4.88vw;
  }
  .sleep__text {
    margin-top: 2.44vw;
    line-height: 3.01vw;
  }
  .sleep__dec {
    width: 79.92vw;
    height: 42.52vw;
    top: 4.31vw;
    right: -17.15vw;
  }
}


@media screen and (max-width: 600px) {
  .sleep__container {
    padding-top: 97.5vw;
    padding-bottom: 21.11vw;
    overflow: hidden;
  }
  .sleep__content {
    width: 100%;
  }
  .sleep__title {
    font-size: 9.72vw;
    line-height: 13.06vw;
  }
  .sleep__text {
    margin-top: 3.33vw;
    line-height: 10.28vw;
  }
  .sleep__dec {
    width: 124.17vw;
    height: 70vw;
    top: 13.89vw;
    right: auto;
    left: -2.5vw;
  }
}