.section__types {
  padding: 118px 0;
}

.types__container {
  justify-content: space-between;
  position: relative;
}

.types__plates {
  width: 680px;
  position: relative;
  z-index: 5;
}

.types__part {
  margin: 0 -20px;
}

.types__plate {
  width: calc(100% / 3 - 40px);
  margin: 0 20px;
  box-shadow: 0 20px 40px rgba(61, 60, 106, 0.1);
  border-radius: 7px;
  padding: 15px;
  padding-top: 30px;
  min-height: 200px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  position: relative;
  z-index: 5;
}

.types__icon {
  height: 65px;

  img {
    height: 100%;
    width: auto;
  }
}

.types__name {
  margin-top: 26px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.types__plus {
  margin-top: 21px;
  padding-top: 75px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    width: 130px;
    height: 38px;
    left: calc(100% / 3);
    transform: translateX(-50%);
    top: 31px;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='132' height='40' viewBox='0 0 132 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M131 39C127.464 31.5316 122.282 24.7456 115.749 19.0296C109.216 13.3135 101.46 8.77924 92.9239 5.68573C84.388 2.59221 75.2392 1 66 1C56.7608 1 47.612 2.59221 39.0761 5.68573C30.5402 8.77925 22.7843 13.3135 16.2512 19.0296C9.71804 24.7456 4.53569 31.5316 1 39' stroke='%23E02222' stroke-width='2'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-background-size: contain;
    background-size: contain;
  }

  &::after {
    content: 'PLUS';
    position: absolute;
    width: 60px;
    height: 60px;
    font-size: 16px;
    border: 3px solid #fff;
    left: calc(100% / 3);
    transform: translateX(-50%);
    top: 0;
    color: #fff;
    font-weight: 500;
    border-radius: 50%;
    background-color: $red;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
  }
}

.types__tag {
  width: 39px;
  height: 16px;
  font-size: 10px;
  line-height: 16px;
  padding-left: 4px;
  top: 27px;
  right: 0;
  background-color: $red;
  color: #fff;
  text-align: center;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
  position: absolute;

  &::before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 0 8px 3px;
    border-color: transparent transparent transparent #ffffff;
    left: 0;
    top: 0;
    bottom: 0;
  }
}

.types__right {
  padding-top: 45px;
  width: 424px;
  position: relative;
  z-index: 5;
}

.types__title {
  font-size: 45px;
  line-height: 60px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.types__desc {
  margin-top: 17px;
  width: 333px;
  line-height: 37px;
}

.types__dec1 {
  width: 693px;
  height: 403px;
  top: 110px;
  right: 83px;
  position: absolute;

  img {
    width: 100%;
    height: 100%;
  }
}

.types__dec2 {
  width: 243px;
  height: 148px;
  top: 292px;
  right: -103px;
  position: absolute;

  img {
    width: 100%;
    height: 100%;
  }
}



@media screen and (max-width: 1230px) {
  .section__types {
    padding: 9.59vw 0;
  }
  .types__plates {
    width: 55.28vw;
  }
  .types__part {
    margin: 0 -1.63vw;
  }
  .types__plate {
    width: calc(100% / 3 - 3.25vw);
    margin: 0 1.63vw;
    box-shadow: 0 1.63vw 3.25vw rgba(61, 60, 106, 0.1);
    border-radius: 0.57vw;
    padding: 1.22vw;
    padding-top: 2.44vw;
    min-height: 16.26vw;
  }
  .types__icon {
    height: 5.28vw;
  }
  .types__name {
    margin-top: 2.11vw;
    font-size: 1.95vw;
    line-height: 2.68vw;
  }
  .types__plus {
    margin-top: 1.71vw;
    padding-top: 6.1vw;
    &::before {
      width: 10.57vw;
      height: 3.09vw;
      left: calc(100% / 3);
      top: 2.52vw;
    }
    &::after {
      width: 4.88vw;
      height: 4.88vw;
      font-size: 1.3vw;
      border: 0.24vw solid #fff;
      left: calc(100% / 3);
    }
  }
  .types__tag {
    width: 3.17vw;
    height: 1.3vw;
    font-size: 0.81vw;
    line-height: 1.3vw;
    padding-left: 0.33vw;
    top: 2.2vw;
    &::before {
      border-width: 0.65vw 0 0.65vw 0.24vw;
    }
  }
  .types__right {
    padding-top: 3.66vw;
    width: 34.47vw;
  }
  .types__title {
    font-size: 3.66vw;
    line-height: 4.88vw;
  }
  .types__desc {
    margin-top: 1.38vw;
    width: 27.07vw;
    line-height: 3.01vw;
  }
  .types__dec1 {
    width: 56.34vw;
    height: 32.76vw;
    top: 8.94vw;
    right: 6.75vw;
  }
  .types__dec2 {
    width: 19.76vw;
    height: 12.03vw;
    top: 23.74vw;
    right: -8.37vw;
  }
}


@media screen and (max-width: 600px) {
  .section__types {
    padding-top: 0;
  }
  .types__container {
    padding-top: 94.17vw;
  }
  .types__plates {
    width: 100%;
    order: 2;
  }
  .types__part {
    margin: 0;
    justify-content: space-between;
  }
  .types__plate {
    width: 43.06vw;
    margin: 0;
    box-shadow: 0 5.56vw 11.11vw rgba(61, 60, 106, 0.1);
    border-radius: 1.94vw;
    padding: 2.22vw;
    padding-top: 3.61vw;
    min-height: 43.06vw;
    margin-bottom: 4.17vw;
  }
  .types__icon {
    height: 15.28vw;
  }
  .types__name {
    margin-top: 4.17vw;
    font-size: 5.56vw;
    line-height: 9.17vw;
  }
  .types__plus {
    margin-top: 0;
    padding-top: 15.28vw;
    &::before {
      width: 26.94vw;
      height: 8.33vw;
      left: calc(100% / 2);
      top: 6.11vw;
    }
    &::after {
      width: 12.22vw;
      height: 12.22vw;
      font-size: 3.33vw;
      border: 0.83vw solid #fff;
      left: calc(100% / 2);
    }
  }
  .types__tag {
    width: 10vw;
    height: 4.17vw;
    font-size: 2.78vw;
    line-height: 4.17vw;
    padding-left: 0.83vw;
    top: 6.11vw;
    &::before {
      border-width: 2.22vw 0 2.22vw 0.83vw;
    }
  }
  .types__right {
    padding-top: 0;
    width: 100%;
    padding-bottom: 8.33vw;
    text-align: center;
  }
  .types__title {
    font-size: 9.72vw;
    line-height: 13.06vw;
  }
  .types__desc {
    margin-top: 3.06vw;
    width: 100%;
    line-height: 10.28vw;
    padding: 0 2.78vw;
  }
  .types__dec1 {
    width: 192.5vw;
    height: 112.78vw;
    top: -28.33vw;
    left: 0;
    right: auto;
  }
  .types__dec2 {
    display: none;
  }
}