.section__comp {
  padding-top: 118px;
  padding-bottom: 115px;
  background-color: #F3F6FF;
}

.comp__container {
  position: relative;
}

.comp__content {
  width: 818px;
  position: relative;
  z-index: 5;
}

.comp__title {
  font-size: 45px;
  line-height: 67px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.comp__text {
  margin-top: 42px;
  line-height: 37px;
  width: 532px;
  span {
    font-weight: 700;
  }
}

.comp__button {
  width: 285px;
  height: 55px;
  font-size: 22px;
  margin-top: 48px;
}

.comp__dec {
  width: 710px;
  height: 482px;
  top: 189px;
  right: -107px;
  position: absolute;
  img {
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 1230px) {
  .section__comp {
    padding-top: 9.59vw;
    padding-bottom: 9.35vw;
  }
  .comp__content {
    width: 66.5vw;
  }
  .comp__title {
    font-size: 3.66vw;
    line-height: 5.45vw;
  }
  .comp__text {
    margin-top: 3.41vw;
    line-height: 3.01vw;
    width: 43.25vw;
  }
  .comp__button {
    width: 23.17vw;
    height: 4.47vw;
    font-size: 1.79vw;
    margin-top: 3.9vw;
  }
  .comp__dec {
    width: 57.72vw;
    height: 39.19vw;
    top: 15.37vw;
    right: -8.7vw;
  }
}


@media screen and (max-width: 600px) {
  .section__comp {
    padding: 0;
  }
  .comp__container {
    padding-top: 95.28vw;
    padding-bottom: 14.17vw;
  }
  .comp__content {
    width: 100%;
  }
  .comp__title {
    font-size: 8.33vw;
    line-height: 13.06vw;
  }
  .comp__text {
    margin-top: 3.89vw;
    line-height: 10.28vw;
    width: 100%;
  }
  .comp__button {
    width: 100%;
    height: 15.28vw;
    font-size: 6.11vw;
    margin-top: 15vw;
  }
  .comp__dec {
    width: 98.33vw;
    height: 66.67vw;
    top: 19.17vw;
    left: -1.39vw;
    right: auto;
  }
}