.section__trans {
  padding-top: 149px;
  padding-bottom: 50px;
  text-align: center;
}

.trans__title {
  font-size: 45px;
  line-height: 67px;
  width: 600px;
  margin: 0 auto;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.trans__text {
  margin-top: 18px;
  font-size: 22px;
  line-height: 36px;
  font-family: 'Quicksand', sans-serif;
}

.trans__list {
  margin-top: 78px;
  justify-content: space-between;
}

.trans__item {
  width: 380px;
  border-radius: 7px;
  padding: 20px;
  padding-bottom: 110px;
  padding-top: 30px;
  margin-bottom: 32px;
  box-shadow: 0 20px 40px rgba(61, 60, 106, 0);
  background-color: #fff;
  position: relative;
  transition: .3s;

  &:hover {
    box-shadow: 0px 20px 40px rgba(61, 60, 106, 0.1);
    z-index: 2;

    .trans__button {
      opacity: 1;
    }
  }
}

.trans__img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  margin: 0 auto;
  position: relative;
  background-color: #F3F6FF;

  img {
    height: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}

.trans__name {
  font-size: 30px;
  line-height: 42px;
  margin-top: 23px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.trans__desc {
  margin-top: 15px;
  font-size: 16px;
  line-height: 30px;
}

.trans__button {
  width: 285px;
  height: 55px;
  bottom: 41px;
  font-size: 22px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: .3s opacity;

  &:hover {
    opacity: .8 !important;
  }
}


@media screen and (max-width: 1230px) {
  .section__trans {
    padding-top: 12.11vw;
    padding-bottom: 4.07vw;
  }
  .trans__title {
    font-size: 3.66vw;
    line-height: 5.45vw;
    width: 48.78vw;
  }
  .trans__text {
    margin-top: 1.46vw;
    font-size: 1.79vw;
    line-height: 2.93vw;
  }
  .trans__list {
    margin-top: 6.34vw;
  }
  .trans__item {
    width: 30.89vw;
    border-radius: 0.57vw;
    padding: 1.63vw;
    padding-bottom: 8.94vw;
    padding-top: 2.44vw;
    margin-bottom: 2.6vw;
    box-shadow: none !important;
  }
  .trans__img {
    width: 7.32vw;
    height: 7.32vw;
    img {
      height: 4.07vw;
    }
  }
  .trans__name {
    font-size: 2.44vw;
    line-height: 3.41vw;
    margin-top: 1.87vw;
  }
  .trans__desc {
    margin-top: 1.22vw;
    font-size: 1.3vw;
    line-height: 2.44vw;
  }
  .trans__button {
    width: 23.17vw;
    height: 4.47vw;
    bottom: 3.33vw;
    font-size: 1.79vw;
    opacity: 1 !important;
  }
}


@media screen and (max-width: 600px) {
  .section__trans {
    padding-top: 20vw;
    padding-bottom: 13.89vw;
  }
  .trans__title {
    font-size: 9.72vw;
    line-height: 13.06vw;
    width: auto;
    margin: 0 -5vw;
  }
  .trans__text {
    margin-top: 3.33vw;
    font-size: 6.11vw;
    line-height: 10vw;
  }
  .trans__list {
    margin: 0 -2.5vw;
    margin-top: 17.22vw;
  }
  .trans__item {
    width: 100%;
    border-radius: 0;
    padding: 0;
    margin-bottom: 16.11vw;
    &:last-child {
      margin-bottom: 0;
      .trans__button {
        margin-top: 7.22vw;
        display: flex;
      }
    }
  }
  .trans__img {
    width: 25vw;
    height: 25vw;
    img {
      height: 13.89vw;
    }
  }
  .trans__name {
    font-size: 8.33vw;
    line-height: 11.67vw;
    margin-top: 7.22vw;
  }
  .trans__desc {
    margin-top: 5.56vw;
    font-size: 4.44vw;
    line-height: 8.33vw;
    padding: 0 2.5vw;
  }
  .trans__button {
    width: calc(100% - 5vw);
    margin: 0 auto;
    position: static;
    height: 15.28vw;
    bottom: auto;
    transform: none;
    font-size: 6.11vw;
    display: none;
  }
}