.section__contact {
  padding-top: 122px;
  padding-bottom: 213px;
}

.contact__container {
  justify-content: space-between;
  position: relative;
}

.contact__left {
  padding-top: 55px;
  width: 431px;
  position: relative;
  z-index: 5;
}

.contact__title {
  font-size: 55px;
  line-height: 67px;
  color: #082854;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.contact__text {
  margin-top: 21px;
}

.contact__button {
  margin-top: 23px;
  width: 310px;
  height: 55px;
  font-size: 22px;
}

.contact__list {
  margin-top: 70px;
  li {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }

    a {
      color: #315176;
      display: flex;
      align-items: center;
      align-content: center;
      transition: .3s;

      img {
        width: 30px;
        margin-right: 24px;
      }

      &:hover {
        color: $red;
      }
    }
  }
}

////////

.contact__right {
  width: 650px;
  padding: 35px;
  padding-bottom: 30px;
  box-shadow: 0px -13px 20px rgba(10, 12, 49, 0.03), 10px 20px 50px rgba(0, 34, 155, 0.06);
  background-color: #fff;
  position: relative;
  z-index: 5;
}

.form__title {
  font-size: 30px;
  line-height: 42px;
  text-align: center;
  color: #082854;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.form__top {
  margin-top: 26px;
  justify-content: space-between;
}

.input {
  height: 50px;
  border-radius: 42px;
  padding: 0 24px;
  font-size: 18px;
  margin-bottom: 20px;
  width: calc(50% - 5px);
  background: #FFFFFF;
  border: 1px solid #082854;
  box-sizing: border-box;
}

.fullInput {
  width: 100%;
}

.form__middle {
  padding-top: 3px;
}

.fmiddle__title {
  font-size: 16px;
  color: #082854;
}

.radio__group {
  margin-top: 10px;
}

.radio__label {
  font-size: 16px;
  line-height: 34px;
  color: #082854;
  display: flex;
  align-items: center;
  align-content: center;
  cursor: pointer;

  input {
    display: none;

    &:checked + .false__radio {
      background-color: $red;

      &::after {
        background-color: $red;
      }
    }
  }
}

.false__radio {
  width: 19px;
  height: 19px;
  margin-right: 11px;
  border-radius: 50%;
  background-color: #082854;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 1px;
    right: 1px;
    top: 1px;
    bottom: 1px;
    border: 4px solid #fff;
    border-radius: 50%;
    background-color: #fff;
  }
}

.form__bottom {
  align-items: center;
  align-content: center;
  justify-content: space-between;

  margin-top: 12px;

  input {
    margin: 0;
    width: calc(50% - 35px);
  }
}

.form__button {
  margin-top: 34px;
  height: 55px;
}


.condecor__1 {
  width: 335px;
  top: -116px;
  right: -70px;
  position: absolute;
}

.condecor__2 {
  width: 695px;
  bottom: -214px;
  left: 45%;
  transform: translateX(-50%);
  position: absolute;
}

.recapcha__cont {
  position: relative;
}

.recaocha__error {
  font-size: 0.6em;
  padding-top: 0.1em;
  line-height: 1.22;
  color: $red;
  display: none;
  position: absolute;
  bottom: 0;
  transform: translateY(100%);

  &.error {
    display: block;
  }
}


