    width: 35.04vw;
  }
  .contact__title {
    font-size: 4.47vw;
    line-height: 5.45vw;
  }
  .contact__text {
    margin-top: 1.71vw;
  }
  .contact__button {
    margin-top: 1.87vw;
    width: 25.2vw;
    height: 4.47vw;
    font-size: 1.79vw;
  }
  .contact__list {
    margin-top: 5.69vw;
    li {
      margin-bottom: 3.25vw;
      a {
        img {
          width: 2.44vw;
          margin-right: 1.95vw;
        }
      }
    }
