  ////////
  .contact__right {
    width: 52.85vw;
    padding: 2.85vw;
    padding-bottom: 2.44vw;
    box-shadow: 0 -1.06vw 1.63vw rgba(10, 12, 49, 0.03), 0.81vw 1.63vw 4.07vw rgba(0, 34, 155, 0.06);
  }
  .form__title {
    font-size: 2.44vw;
    line-height: 3.41vw;
  }
