    margin-top: 2.11vw;
  }
  .input {
    height: 4.07vw;
    border-radius: 3.41vw;
    padding: 0 1.95vw;
    font-size: 1.46vw;
    margin-bottom: 1.63vw;
    width: calc(50% - 0.41vw);
  }
  .fullInput {
    width: 100%;
  }
  .form__middle {
    padding-top: 0.24vw;
  }
  .fmiddle__title {
    font-size: 1.3vw;
  }
  .radio__group {
    margin-top: 0.81vw;
  }
  .radio__label {
    font-size: 1.3vw;
    line-height: 2.76vw;
  }
  .false__radio {
    width: 1.54vw;
    height: 1.54vw;
    margin-right: 0.89vw;
    &::after {
      border-width: 0.33vw;
    }
  }
  .form__bottom {
    justify-content: center;
    .input {
      width: 100%;
      margin-bottom: 1.63vw;
    }
  }
  .form__button {
    margin-top: 2.76vw;
    height: 4.47vw;
  }
  .condecor__1 {
    width: 27.24vw;
    top: -9.43vw;
    right: -5.69vw;
  }
  .condecor__2 {
    width: 56.5vw;
    bottom: -17.4vw;
  }
}


