    padding-bottom: 16.11vw;
    display: flex;
    flex-direction: column;
  }
  .contact__title {
    font-size: 10.83vw;
    line-height: 14.44vw;
    text-align: center;
  }
  .contact__text {
    margin-top: 6.39vw;
    text-align: center;
  }
  .contact__button {
    margin-top: 13.89vw;
    width: 100%;
    height: 15.28vw;
    font-size: 6.11vw;
    order: 2;
  }
  .contact__list {
    margin-top: 9.72vw;
    margin-left: 5vw;
    li {
      margin-bottom: 5.56vw;
      a {
        img {
          width: 8.33vw;
          margin-right: 6.67vw;
        }
      }
    }
  }
  ////////
  .contact__right {
    width: 100vw;
    margin: 0 -5vw;
    padding: 5vw;
    padding-top: 15.28vw;
    padding-bottom: 12.5vw;
    box-shadow: 0 -3.61vw 5.56vw rgba(10, 12, 49, 0.03), 2.78vw 5.56vw 13.89vw rgba(0, 34, 155, 0.06);
  }
  .form__title {
    font-size: 6.67vw;
    line-height: 11.67vw;
  }
  .form__top {
    margin-top: 5.56vw;
  }
  .input {
    height: 13.89vw;
    border-radius: 11.67vw;
    padding: 0 6.67vw;
    font-size: 5vw;
    margin-bottom: 4.17vw;
    width: 100%;
  }
  .form__middle {
    padding-top: 2.78vw;
  }
  .fmiddle__title {
    font-size: 4.44vw;
  }
  .radio__group {
    margin-top: 4.44vw;
  }
  .radio__label {
    font-size: 4.44vw;
    line-height: 9.44vw;
  }
  .false__radio {
    width: 5.28vw;
    height: 5.28vw;
    margin-right: 3.06vw;
    &::after {
      border-width: 1.11vw;
    }
  }
  .form__bottom {
    .input {
      margin-top: 5.56vw;
      margin-bottom: 6.67vw;
    }
  }
  .form__button {
    margin-top: 7.56vw;
    height: 15.28vw;
  }
  .condecor__1 {
