.simple__content {
  padding: 69px 0;
  justify-content: center;
  text-align: center;
}

.simple__inner {
  width: 693px;
}

.simple__title {
  font-size: 45px;
  line-height: 67px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.simple__text {
  margin-top: 14px;
}

.simple__button {
  margin: 0 auto;
  margin-top: 28px;
  height: 55px;
  font-size: 22px;
  width: 325px;
}




@media screen and (max-width: 1230px) {
  .simple__content {
    padding: 5.61vw 0;
  }
  .simple__inner {
    width: 56.34vw;
  }
  .simple__title {
    font-size: 3.66vw;
    line-height: 5.45vw;
  }
  .simple__text {
    margin-top: 1.14vw;
  }
  .simple__button {
    margin: 0 auto;
    margin-top: 2.28vw;
    height: 4.47vw;
    font-size: 1.79vw;
    width: 26.42vw;
  }
}


@media screen and (max-width: 600px) {
  .simple__content {
    padding-top: 8.89vw;
    padding-bottom: 13.33vw;
  }
  .simple__inner {
    width: 100%;
  }
  .simple__title {
    font-size: 8.33vw;
    line-height: 11.67vw;
  }
  .simple__text {
    margin-top: 3.33vw;
  }
  .simple__button {
    margin: 0 auto;
    margin-top: 9.44vw;
    height: 15.28vw;
    font-size: 6.11vw;
    width: 100%;
  }
}