.section__choose {
  padding-top: 70px;
  padding-bottom: 105px;
  background-color: #F3F6FF;
  text-align: center;
}

.shoose__title {
  font-size: 45px;
  line-height: 67px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.choose__text {
  margin: 0 auto;
  margin-top: 15px;
  width: 738px;
}

.choose__list {
  margin: 0 -25px;
  margin-top: 36px;
  justify-content: center;
}

.choose__item {
  width: calc(25% - 50px);
  margin: 0 25px;
}

.choose__img {
  width: 175px;
  height: 175px;
  margin: 0 auto;
  border-radius: 50%;
  position: relative;
  background-color: #fff;

  img {
    width: 90px;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    position: absolute;
  }
}

.shoose__name {
  margin-top: 27px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.choose__desc {
  margin-top: 29px;
  font-size: 16px;
  line-height: 28px;
}


@media screen and (max-width: 1230px) {
  .section__choose {
    padding-top: 5.69vw;
    padding-bottom: 8.54vw;
  }
  .shoose__title {
    font-size: 3.66vw;
    line-height: 5.45vw;
  }
  .choose__text {
    margin: 0 auto;
    margin-top: 1.22vw;
    width: 60vw;
  }
  .choose__list {
    margin: 0 -2.03vw;
    margin-top: 2.93vw;
  }
  .choose__item {
    width: calc(25% - 4.07vw);
    margin: 0 2.03vw;
  }
  .choose__img {
    width: 14.23vw;
    height: 14.23vw;
    img {
      width: 7.32vw;
    }
  }
  .shoose__name {
    margin-top: 2.2vw;
    font-size: 1.95vw;
    line-height: 2.68vw;
  }
  .choose__desc {
    margin-top: 2.36vw;
    font-size: 1.3vw;
    line-height: 2.28vw;
  }
}


@media screen and (max-width: 600px) {
  .section__choose {
    padding-top: 19.44vw;
    padding-bottom: 11.11vw;
  }
  .shoose__title {
    font-size: 9.72vw;
    line-height: 13.06vw;
  }
  .choose__text {
    margin: 0 auto;
    margin-top: 4.44vw;
    width: 100%;
  }
  .choose__list {
    margin: 0;
    margin-top: 15vw;
    padding: 0 2.78vw;
  }
  .choose__item {
    width: 100%;
    margin: 0;
    margin-bottom: 16.67vw;
  }
  .choose__img {
    width: 48.61vw;
    height: 48.61vw;
    img {
      width: 25vw;
    }
  }
  .shoose__name {
    margin-top: 7.5vw;
    font-size: 6.67vw;
    line-height: 9.17vw;
  }
  .choose__desc {
    margin-top: 5vw;
    font-size: 4.44vw;
    line-height: 7.78vw;
  }
}
