.section__orbit {
  padding-top: 137px;
  overflow: hidden;
}

.orbit__cintainer {
  height: 564px;
  position: relative;
}

.orbit__deccont {
  width: 850px;
  height: 850px;
  top: 0;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.orbit__sirkle {
  border: 1px dashed #ABB9D6;
  border-radius: 50%;
  position: absolute;
  transform-origin: center center;
  transform: rotate(0);

  div {
    position: absolute;

    img {
      width: 100%;
      height: 100%;
    }
  }
}

.orbit__cir1 {
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.orbit__cir2 {
  width: 700px;
  height: 700px;
  top: calc(50% - 350px);
  left: calc(50% - 350px);
}

.orbit__cir3 {
  width: 550px;
  height: 550px;
  top: calc(50% - 275px);
  left: calc(50% - 275px);
}

.orbit__cir4 {
  width: 400px;
  height: 400px;
  top: calc(50% - 200px);
  left: calc(50% - 200px);
}

.planet_1 {
  width: 42px;
  height: 42px;
  left: 205px;
  top: 27px;
}

.planet_2 {
  width: 34px;
  height: 34px;
  right: 242px;
  top: 17px;
}

.planet_3 {
  width: 37px;
  height: 37px;
  right: 42px;
  top: 187px;
}

.planet_4 {
  width: 49px;
  height: 49px;
  left: 56px;
  top: 56px;
}

.planet_5 {
  width: 60px;
  height: 60px;
  right: 174px;
  top: -18px;
}

.planet_6 {
  width: 41px;
  height: 41px;
  left: 94px;
  top: 2px;
}

.orbit__buttons {
  width: 850px;
  position: relative;
  margin: 0 auto;
  z-index: 7;
}

.orbit__button {
  height: 75px;
  background-color: #fff;
  box-shadow: 0 10px 40px rgba(3, 18, 72, 0.12);
  border-radius: 65px;
  min-width: 127px;
  padding: 0 32px;
  transform: translateX(-50%);
  display: inline-flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  position: absolute;
  cursor: pointer;

  &::after {
    content: '';
    position: absolute;
    left: -10px;
    bottom: -10px;
    top: -10px;
    right: -10px;
    border: 10px solid #F3F5FB;
    border-radius: 82px;
    opacity: 0;
    transition: .3s;
  }

  img {
    max-height: 46px;
    max-width: 155px;
    display: block;
  }

  &.orbitActive {
    &::after {
      opacity: 1;
    }
  }
}

.orbit__hidden {
  display: none;
}

.orbutton__1 {
  top: -38px;
  left: 50%;
}

.orbutton__2 {
  top: 97px;
  left: 108px;
}

.orbutton__3 {
  top: 117px;
  left: 352px;
}

.orbutton__4 {
  top: 267px;
  left: 88px;
}

.orbutton__5 {
  top: 60px;
  right: -94px;
}

.orbutton__6 {
  top: 267px;
  right: -142px;
}

.orbutton__7 {
  top: 181px;
  right: 51px;
}

.orbit__pastcont {
  height: 340px;
  width: 400px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  text-align: center;
  z-index: 5;
}

.orbit__pname {
  font-size: 30px;
  line-height: 36px;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.orbit__ptext {
  margin-top: 13px;
  font-size: 22px;
}

.orbit__bg {
  height: 698px;
  bottom: -347px;
  width: 100vw;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.65) 22.94%, #FFFFFF 100%);
}



