.section__login {
  border-bottom: 1px solid #D2D8F0;
  background-color: #F3F6FF;
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    height: 720px;
    width: 2920px;
    min-width: 100vw;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: #ffffff;
    border-radius: 0 0 40% 40%;
  }
}

.login__content {
  padding-top: 117px;
  padding-bottom: 100px;
  flex-direction: column;
  align-items: center;
  align-content: center;
  position: relative;
  z-index: 5;

  .contact__right {
    margin-top: 85px;
  }
}

.login__top {
  width: 432px;
  text-align: center;
}

.login__title {
  font-size: 55px;
  line-height: 67px;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.login__text {
  margin-top: 21px;
}

.login__button {
  margin: 0 auto;
  margin-top: 35px;
  width: 310px;
  height: 55px;
  font-size: 22px;
}


@media screen and (max-width: 1230px) {
  .section__login {
    &::before {
      height: 73.75vw;
      width: 200.21vw;
    }
  }
  .login__content {
    padding-top: 15.23vw;
    padding-bottom: 13.02vw;
    .contact__right {
      margin-top: 11.07vw;
    }
  }
  .login__top {
    width: 56.25vw;
  }
  .login__title {
    font-size: 7.16vw;
    line-height: 8.72vw;
  }
  .login__text {
    margin-top: 2.73vw;
  }
  .login__button {
    margin: 0 auto;
    margin-top: 4.56vw;
    width: 40.36vw;
    height: 7.16vw;
    font-size: 2.86vw;
  }
}

@media screen and (max-width: 600px) {
  .section__login {
    &::before {
      height: 77.78vw;
      width: 200.21vw;
    }
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      height: 15vw;
      background-color: #fff;
    }
  }
  .login__content {
    padding-top: 18.33vw;
    padding-bottom: 11.11vw;
    .contact__right {
      margin-top: 11.11vw;
      padding-bottom: 1.67vw;
    }
  }
  .login__top {
    width: 100%;
  }
  .login__title {
    font-size: 10.83vw;
    line-height: 14.44vw;
  }
  .login__text {
    width: 82vw;
    margin: 0 auto;
    margin-top: 6.67vw;
  }
  .login__button {
    margin: 0 auto;
    margin-top: 28.33vw;
    width: 100%;
    height: 15.28vw;
    font-size: 6.11vw;
  }
}