.section__feed {
  padding-top: 154px;
  padding-bottom: 125px;
  text-align: center;
  overflow: hidden;
}

.fed__title {
  width: 600px;
  font-size: 45px;
  line-height: 67px;
  margin: 0 auto;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.feed__text {
  margin-top: 18px;
  font-size: 22px;
  line-height: 36px;
  font-family: 'Quicksand', sans-serif;
}

.feed__container {
  margin-top: 96px;
  position: relative;
}

.feed__decor {
  position: relative;
  z-index: 1;
}

.feeddec {
  border-radius: 50%;
  position: absolute;
}

