}

.feed__reviews {
  padding-top: 443px;
  position: relative;
  z-index: 4;
}

.feedrev {
  position: absolute;
  border-radius: 50%;
  cursor: pointer;
  z-index: 7;

  .fedrev__img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 50%;
    position: relative;
  }

  &::after {
    content: '';
    position: absolute;
    left: -4px;
    top: -4px;
    right: -4px;
    bottom: -4px;
    border-width: 2px;
    border-style: solid;
    box-sizing: border-box;
    border-radius: 50%;

  }

  img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &.active {
    width: 180px;
    height: 180px;
    top: 220px;
    left: calc(50% - 90px);
    transition: .4s;
    transform: translateX(0) translateY(0) !important;

    &::after {
      right: -13px;
      left: -13px;
      bottom: -13px;
      top: -13px;
      border: 7px solid #fff;
    }
  }
}

.trans {
  transition: .4s;
}

.feed__hidden {
  display: none;
}

.fr_1 {
  width: 92px;
