.feed__revcint {
  position: relative;
}

.feed__sircle {
  width: 620px;
  height: 620px;
  top: -438px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  position: absolute;
  background-color: #F3F6FF;

  &::after {
    position: absolute;
    content: '';
    width: 424px;
    height: 424px;
    border: 3px solid #0040FF;
    border-radius: 50%;
    opacity: .1;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}

.feed__mainplace {
  width: 700px;
  box-shadow: 0 20px 40px rgba(49, 81, 118, 0.1);
  border-radius: 10px;
  padding: 24px 38px;
  padding-bottom: 21px;
  margin: 0 auto;
  position: relative;
  background-color: #fff;

  .feed__revtxt {
    font-size: 20px;
    line-height: 36px;
    color: #315176;
  }

  .feed__revname {
    margin-top: 20px;
    font-size: 24px;
    line-height: 36px;
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
  }

  .feed__revpos {
    font-size: 18px;
    line-height: 24px;
    font-family: 'Quicksand', sans-serif;
  }

  &::before {
    content: '';
    position: absolute;
    top: -25px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 35px 37px 35px;
    border-color: transparent transparent #ffffff transparent;
    left: 50%;
    transform: translateX(-50%);
  }
}

.feed__map {
  width: 1350px;
  height: 546px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  img {
    width: 100%;
    height: 100%;
  }
}




