.section__client {
  background-color: #F3F6FF;
  overflow: hidden;
}

.client__content {
  padding-top: 95px;
  padding-bottom: 143px;
  position: relative;
}

.client__title {
  font-size: 45px;
  line-height: 67px;
  font-weight: 700;
  text-align: center;
  font-family: 'Quicksand', sans-serif;
  position: relative;
  z-index: 2;
}

.client__text {
  margin: 0 auto;
  margin-top: 4px;
  width: 660px;
  font-size: 22px;
  line-height: 36px;
  text-align: center;
  position: relative;
  z-index: 2;
}

.client__listtitle {
  margin-top: 72px;
  font-size: 30px;
  line-height: 67px;
  font-weight: 700;
  text-align: center;
  font-family: 'Quicksand', sans-serif;
  position: relative;
  z-index: 2;
}

.client__list {
  margin-top: 29px;
  justify-content: space-between;
  position: relative;
  z-index: 2;
}

.client__item {
  padding-left: 67px;
  font-size: 24px;
  line-height: 36px;
  margin-bottom: 43px;
  position: relative;

  &:nth-child(3n - 2) {
    width: 372px;
  }

  &:nth-child(3n - 1) {
    width: 293px;
  }

  &:nth-child(3n) {
    width: 274px;
  }
}

.client__num {
  font-size: 28px;
  position: absolute;
  left: 0;
  top: 0;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.client__dec {
  width: 1132px;
  height: 678px;
  left: -370px;
  bottom: 0;
  position: absolute;

  img {
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 1230px) {
  .client__content {
    padding-top: 7.72vw;
    padding-bottom: 11.63vw;
  }
  .client__title {
    font-size: 3.66vw;
    line-height: 5.45vw;
  }
  .client__text {
    margin: 0 auto;
    margin-top: 0.33vw;
    width: 53.66vw;
    font-size: 1.79vw;
    line-height: 2.93vw;
  }
  .client__listtitle {
    margin-top: 5.85vw;
    font-size: 2.44vw;
    line-height: 5.45vw;
  }
  .client__list {
    margin-top: 2.36vw;
  }
  .client__item {
    padding-left: 5.45vw;
    font-size: 1.95vw;
    line-height: 2.93vw;
    margin-bottom: 3.5vw;
    &:nth-child(3n - 2) {
      width: 30.24vw;
    }
    &:nth-child(3n - 1) {
      width: 23.82vw;
    }
    &:nth-child(3n) {
      width: 22.28vw;
    }
  }
  .client__num {
    font-size: 2.28vw;
  }
  .client__dec {
    width: 92.03vw;
    height: 55.12vw;
    left: -30.08vw;
  }
}


@media screen and (max-width: 600px) {
  .client__content {
    padding-top: 16.11vw;
    padding-bottom: 16.11vw;
  }
  .client__title {
    font-size: 9.72vw;
    line-height: 13.06vw;
  }
  .client__text {
    margin-top: 3.89vw;
    width: 100%;
    font-size: 6.11vw;
    line-height: 10vw;
  }
  .client__listtitle {
    margin-top: 11.94vw;
    font-size: 8.33vw;
    line-height: 11.67vw;
  }
  .client__list {
    margin-top: 11.67vw;
    display: block;
  }
  .client__item {
    padding-left: 19.44vw;
    font-size: 6.67vw;
    line-height: 10vw;
    margin-bottom: 10vw;
    width: 100% !important;
  }
  .client__num {
    font-size: 7.78vw;
  }
  .client__dec {
    width: 203.61vw;
    height: 121.94vw;
    left: -26.11vw;
  }
}