.section__padv {
  padding-top: 57px;
  padding-bottom: 125px;
  background-color: #F3F6FF;
}

.padv__carousel {
  justify-content: center;
  display: flex;
}

.padv__item {
  width: 261px;
  margin: 0 30px;
  text-align: center;

  &:nth-child(3n + 2) {
    width: 320px;
  }
}

.padv__img {
  width: 175px;
  height: 175px;
  border-radius: 50%;
  background-color: #fff;
  margin: 0 auto;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;

  img {
    width: 73px;
  }
}

.padv__text {
  margin-top: 27px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}


@media screen and (max-width: 1230px) {
  .section__padv {
    padding-top: 4.63vw;
    padding-bottom: 10.16vw;
  }
  .padv__item {
    width: 21.22vw;
    margin: 0 2.44vw;
    &:nth-child(3n + 2) {
      width: 26.02vw;
    }
  }
  .padv__img {
    width: 14.23vw;
    height: 14.23vw;
    img {
      width: 5.93vw;
    }
  }
  .padv__text {
    margin-top: 2.2vw;
    font-size: 1.95vw;
    line-height: 2.68vw;
  }
}

@media screen and (max-width: 600px) {
  .section__padv {
    padding-top: 13.89vw;
    padding-bottom: 24.72vw;
  }
  .padv__carousel {
    width: 73.61vw;
    margin: 0 auto;
    display: block;
    position: relative;
    .owl-dots {
      position: absolute;
      bottom: -13.33vw;
      display: flex;
      justify-content: center;
      left: 0;
      right: 0;
      div {
        width: 4.17vw;
        height: 4.17vw;
        border: 1px solid $red;
        margin: 0 1.39vw;
        border-radius: 50%;
        transition: .3s;
        &.active {
          background-color: $red;
        }
      }
    }
  }
  .padv__item {
    width: 100% !important;
    margin: 0;
  }
  .padv__img {
    width: 48.61vw;
    height: 48.61vw;
    img {
      width: 20vw;
    }
  }
  .padv__text {
    margin-top: 7.5vw;
    font-size: 6.67vw;
    line-height: 9.17vw;
    color: #082854 !important;
  }
}