.section__partnears {
  box-shadow: 10px 20px 30px rgba(0, 34, 155, 0.04);
  border-top: 1px solid #D2D8F0;
  position: relative;
  z-index: 2;
}

.partnears__content {
  padding-top: 47px;
  padding-bottom: 42px;
  padding-left: 90px;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}

.partnears__text {
  width: 296px;
  font-size: 36px;
  line-height: 45px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.partnears__logos {
  width: calc(100% - 296px);
  height: 165px;
  padding-top: 19px;
  padding-left: 87px;
  border-left: 1px solid #D2D8F0;
  align-content: space-between;
  justify-items: center;
  flex-direction: column;

  li {
    margin-bottom: 27px;
    height: 46px;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
  }

  img {
    max-width: 155px;
    max-height: 46px;
    width: auto;
    height: auto;
  }
}


@media screen and (max-width: 1230px) {
  .section__partnears {
    box-shadow: 0.81vw 1.63vw 2.44vw rgba(0, 34, 155, 0.04);
  }
  .partnears__content {
    padding-top: 3.82vw;
    padding-bottom: 3.41vw;
    padding-left: 7.32vw;
  }
  .partnears__text {
    width: 24.07vw;
    font-size: 2.93vw;
    line-height: 3.66vw;
  }
  .partnears__logos {
    width: calc(100% - 24.07vw);
    height: 13.5vw;
    padding-top: 1.54vw;
    padding-left: 7.07vw;
    li {
      margin-bottom: 2.2vw;
      height: 3.74vw;
    }
    img {
      max-width: 12.6vw;
      max-height: 3.74vw;
    }
  }
}


@media screen and (max-width: 600px) {
  .section__partnears {
    box-shadow: none;
  }
  .partnears__content {
    padding-top: 17.5vw;
    padding-bottom: 6.39vw;
    padding-left: 0;
    display: block;
  }
  .partnears__text {
    width: 100%;
    font-size: 8.33vw;
    line-height: 11.67vw;
    text-align: center;
  }
  .partnears__logos {
    width: 100%;
    height: auto;
    padding-top: 16.67vw;
    padding-left: 0;
    flex-direction: row;
    border: none;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      width: 44.72vw;
      left: 50%;
      transform: translateX(-50%);
      height: 1px;
      background-color: #D2D8F0;
    }
    li {
      margin-bottom: 12.5vw;
      height: auto;
      width: 50%;
      &:nth-child(1) {
        order: 4;
      }
      &:nth-child(2) {
        order: 0;
      }
      &:nth-child(3) {
        order: 5;
      }
      &:nth-child(4) {
        order: 1;
      }
      &:nth-child(5) {
        order: 0;
      }
      &:nth-child(6) {
        order: 2;
      }
      &:nth-child(7) {
        order: 6;
      }
    }
    img {
      max-width: 43.06vw;
      max-height: 12.78vw;
    }
  }
}