.more__content {
  padding-top: 85px;
  padding-bottom: 72px;
}

.more__left {
  width: 427px;
}

.more__title {
  font-size: 45px;
  line-height: 60px;
  font-weight: 700;
  font-family: 'Quicksand', sans-serif;
}

.more__text {
  margin-top: 28px;
}

.more__right {
  margin-top: 18px;
  width: 479px;
  height: 325px;
  margin-left: 72px;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    width: 433px;
    height: 313px;
    top: 42px;
    left: 76px;
    background-color: #F1F1F3;
  }
}

.more__imgcont {
  height: 100%;
  overflow: hidden;
  position: relative;
  z-index: 2;
}


@media screen and (max-width: 1230px) {
  .more__content {
    padding-top: 6.91vw;
    padding-bottom: 5.85vw;
  }
  .more__left {
    width: 34.72vw;
  }
  .more__title {
    font-size: 3.66vw;
    line-height: 4.88vw;
  }
  .more__text {
    margin-top: 2.28vw;
  }
  .more__right {
    margin-top: 1.46vw;
    width: 38.94vw;
    height: 26.42vw;
    margin-left: 5.85vw;
    &::after {
      width: 35.2vw;
      height: 25.45vw;
      top: 3.41vw;
      left: 6.18vw;
    }
  }
}

@media screen and (max-width: 600px) {
  .more__content {
    padding-top: 13.33vw;
    padding-bottom: 0;
    display: block;
  }
  .more__left {
    width: 100%;
  }
  .more__title {
    font-size: 8.33vw;
    line-height: 11.67vw;
  }
  .more__text {
    margin-top: 4.17vw;
  }
  .more__right {
    margin: 0 -5vw;
    margin-top: 13.06vw;
    width: auto;
    height: 67.78vw;
    &::after {
      display: none;
    }
  }
}