.header {
  box-shadow: 10px 20px 50px rgba(0, 34, 155, 0.06);
  position: sticky;
  top: 0;
  z-index: 20;
  background-color: #fff;
}

.heder__content {
  height: 72px;
  align-items: center;
  align-content: center;
  justify-content: space-between;
}

.header__logo {
  align-items: center;
  align-content: center;

  img {
    width: 38px;
    height: 49px;
    margin-right: 6px;
  }

  span {
    font-size: 24px;
    color: #434242;
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;

    span {
      color: $red;
    }
  }
}

.jeader__nav {
  li {
    margin-right: 56px;

    &:last-child {
      margin-right: 0;
    }

    a {
      font-size: 18px;
      letter-spacing: 0.02em;
      color: #082854;
      font-family: 'Quicksand', sans-serif;
      font-weight: 500;
      transition: .3s;

      &:hover {
        color: $red;
      }
    }
  }
}

.active {
  color: $red !important;
}

.enterLink {
  color: $red !important;
  display: flex;
  align-items: center;
  align-content: center;

  img {
    width: 22px;
    margin-right: 7px;
  }
}

.heaer__hidden {
  display: none;
}


@media screen and (max-width: 1230px) {
  .header {
    box-shadow: 0.81vw 1.63vw 4.07vw rgba(0, 34, 155, 0.06);
  }
  .heder__content {
    height: 5.85vw;
  }
  .header__logo {
    img {
      width: 3.09vw;
      height: 3.98vw;
      margin-right: 0.49vw;
    }
    span {
      font-size: 1.95vw;
    }
  }
  .jeader__nav {
    li {
      margin-right: 4.55vw;
      a {
        font-size: 1.46vw;
      }
    }
  }
  .enterLink {
    img {
      width: 1.79vw;
      margin-right: 0.57vw;
    }
  }
}


@media screen and (max-width: 600px) {
  .header {
    box-shadow: none;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      box-shadow: 2.78vw 5.56vw 13.89vw rgba(0, 34, 155, 0.06);
      z-index: 28;
      background-color: #fff;
    }
  }
  .heder__content {
    height: 13.89vw;
  }
  .header__logo {
    position: relative;
    z-index: 30;
    img {
      width: 8.33vw;
      height: 10.83vw;
      margin-right: 1.67vw;
    }
    span {
      font-size: 5.83vw;
    }
  }
  .header__right {
    position: fixed;
    right: 0;
    bottom: 0;
    top: 13.89vw;
    background-color: #fff;
    width: 71.39vw;
    z-index: 18;
    overflow: hidden;
    opacity: 0;
    transform: translateX(100%);
    transition: .3s;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      right: 71.39vw;
      width: calc(100vw - 71.39vw);
      background-color: rgba(67, 127, 236, 0.2);
      opacity: .3;
    }
  }
  .jeader__nav {
    display: block;
    padding: 13.33vw 8.33vw;
    li {
      margin-right: 0;
      margin-bottom: 6.94vw;
      &:last-child {
        margin-bottom: 0;
      }
      a {
        font-size: 5vw;
      }
    }
  }
  .enterLink {
    img {
      width: 6.11vw;
      margin-right: 1.94vw;
    }
  }
  ////////
  .heaer__hidden {
    display: block;
    position: relative;
    z-index: 30;
  }
  .hamburger {
    width: 8.33vw;
    height: 5vw;
    display: block;
    position: relative;
    z-index: 22;

    span {
      width: 8.33vw;
      height: 0.83vw;
      border-radius: 0.83vw;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      position: absolute;
      background-color: #082854;
      transition: .3s;

      &:first-child {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translateY(-50%) translateX(-50%);
      }
      &:last-child {
        width: 5.56vw;
        left: auto;
        right: 0;
        transform: none;
        bottom: 0;
      }
    }
  }
  .openedNav {
    .hamburger {
      span {
        &:first-child {
          top: 50%;
          transform: translateY(-50%) translateX(-50%) rotate(45deg);
        }
        &:nth-child(2) {

          transform: translateY(-50%) translateX(-50%) rotate(-45deg);
        }
        &:last-child {
          opacity: 0;
        }
      }
    }
  }

  body.openedNav {
    .header__right {
      overflow: visible;
      opacity: 1;
      transform: translateX(0);
    }
  }
}